body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

/* makes link have a static style */
a {
  color: #61dafb;
  text-decoration: none;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.calendar {
  background-color: lightblue;
  border-radius: 3px;
  padding: 5%;
  width: -webkit-max-content;
  width: max-content;

  -webkit-user-select: none; /* Webkit */ /* Firefox */
  -ms-user-select: none; /* IE 10 */

  /* Currently not supported in Opera but will be soon */
  -o-user-select: none;
  user-select: none;

  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

/* For the arrow buttons on Calendar */
.arrow-button {
  font-size: larger;
  font-weight: bold;
  background-color: white;
}

/* For Month and Year Appearance */
#month-year-text {
  font-family: cursive;
  font-size: 36pt;
  margin: 3%;
}

/* For Day of Week Text "Su", etc. */
.day-week-text {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: x-large;
  text-decoration: underline;
}
/* generic calendar tile */
.tile {
  height: 75px;
  width: 75px;
  outline: 1px solid black;
  display: table-cell;
  background-color: lavender;
  border-radius: 3px;

  vertical-align: middle;
  
  transition: 0.1s;
}

/* Makes an outline appear when hovering over a tile */
.tile:hover {
  outline: 5px solid rgb(70, 70, 70);
}

.tile-num {
  text-align: center;
  font-size: 36px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.tile-mood {
  position: relative;
  display: unset;
  top: 20px;
  left: 5px;
}

/* selected tile will have a different color */
.selected-tile {
  background-color: lightgreen;
}

.block--styling{
    background-color: lightblue;
    display: inline-block;
    margin: 25px;
    height: 500px;
    width: 300px;
    border-radius: 10px;
}

